import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Typography,
  Avatar,
  Stack,
  IconButton,
  Fade,
  useTheme,
} from "@mui/material";
import { GitHub, LinkedIn, Email } from "@mui/icons-material";
import ProfileImage from "../assets/profilePic.jpg";

export function HeroSection() {
  const theme = useTheme();
  const [showScrollIndicator, setShowScrollIndicator] = useState(true);

  useEffect(() => {
    const handleScroll = () => {
      // Hide indicator when user scrolls down more than 100px
      if (window.scrollY > 100) {
        setShowScrollIndicator(false);
      } else {
        setShowScrollIndicator(true);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <Box
      sx={{
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
        background:
          theme.palette.mode === "dark"
            ? "linear-gradient(135deg, #1e293b 0%, #0f172a 50%, #020617 100%)"
            : "linear-gradient(135deg, #dbeafe 0%, #e0e7ff 50%, #fce7f3 100%)",
        position: "relative",
        overflow: "hidden",
      }}
    >
      {/* Animated background elements */}
      <Box
        sx={{
          position: "absolute",
          width: "600px",
          height: "600px",
          borderRadius: "50%",
          background:
            theme.palette.mode === "dark"
              ? "radial-gradient(circle, rgba(59,130,246,0.15) 0%, transparent 70%)"
              : "radial-gradient(circle, rgba(37,99,235,0.2) 0%, transparent 70%)",
          top: "-300px",
          right: "-200px",
          animation: "float 20s ease-in-out infinite",
          "@keyframes float": {
            "0%, 100%": { transform: "translate(0, 0)" },
            "50%": { transform: "translate(-50px, 50px)" },
          },
        }}
      />
      <Box
        sx={{
          position: "absolute",
          width: "400px",
          height: "400px",
          borderRadius: "50%",
          background:
            theme.palette.mode === "dark"
              ? "radial-gradient(circle, rgba(139,92,246,0.15) 0%, transparent 70%)"
              : "radial-gradient(circle, rgba(124,58,237,0.2) 0%, transparent 70%)",
          bottom: "-100px",
          left: "-100px",
          animation: "float 15s ease-in-out infinite",
          animationDelay: "2s",
        }}
      />

      <Container maxWidth="lg" sx={{ position: "relative", zIndex: 1 }}>
        <Fade in timeout={1000}>
          <Box sx={{ textAlign: "center" }}>
            <Avatar
              src={ProfileImage}
              sx={{
                width: 180,
                height: 180,
                mx: "auto",
                mb: 4,
                border: `4px solid ${
                  theme.palette.mode === "dark"
                    ? "rgba(59, 130, 246, 0.3)"
                    : "rgba(37, 99, 235, 0.3)"
                }`,
                boxShadow:
                  theme.palette.mode === "dark"
                    ? "0 20px 60px rgba(59, 130, 246, 0.4)"
                    : "0 20px 60px rgba(37, 99, 235, 0.3)",
                transition: "transform 0.3s",
                "&:hover": {
                  transform: "scale(1.05)",
                },
              }}
            />

            <Typography
              variant="h1"
              sx={{
                fontSize: { xs: "2.5rem", sm: "3.5rem", md: "5rem" },
                mb: 2,
                background:
                  theme.palette.mode === "dark"
                    ? "linear-gradient(135deg, #3b82f6 0%, #8b5cf6 100%)"
                    : "linear-gradient(135deg, #2563eb 0%, #7c3aed 100%)",
                backgroundClip: "text",
                WebkitBackgroundClip: "text",
                WebkitTextFillColor: "transparent",
                animation: "fadeInUp 0.8s ease-out",
                "@keyframes fadeInUp": {
                  from: {
                    opacity: 0,
                    transform: "translateY(30px)",
                  },
                  to: {
                    opacity: 1,
                    transform: "translateY(0)",
                  },
                },
              }}
            >
              Ralph Lauren Ambatali
            </Typography>

            <Typography
              variant="h4"
              sx={{
                mb: 3,
                color: theme.palette.text.primary,
                fontWeight: 600,
                animation: "fadeInUp 0.8s ease-out 0.2s backwards",
              }}
            >
              Front-End Developer
            </Typography>

            <Typography
              variant="h6"
              sx={{
                mb: 5,
                maxWidth: 700,
                mx: "auto",
                color: theme.palette.text.secondary,
                fontWeight: 400,
                lineHeight: 1.6,
                animation: "fadeInUp 0.8s ease-out 0.4s backwards",
              }}
            >
              Building elegant solutions to complex problems with modern web
              technologies
            </Typography>

            <Stack
              direction="row"
              spacing={2}
              justifyContent="center"
              sx={{ animation: "fadeInUp 0.8s ease-out 0.6s backwards" }}
            >
              <IconButton
                sx={{
                  bgcolor:
                    theme.palette.mode === "dark"
                      ? "rgba(59, 130, 246, 0.1)"
                      : "rgba(37, 99, 235, 0.1)",
                  color: theme.palette.primary.main,
                  border: `2px solid ${
                    theme.palette.mode === "dark"
                      ? "rgba(59, 130, 246, 0.3)"
                      : "rgba(37, 99, 235, 0.3)"
                  }`,
                  width: 56,
                  height: 56,
                  transition: "all 0.3s",
                  "&:hover": {
                    bgcolor: theme.palette.primary.main,
                    color: "white",
                    transform: "translateY(-4px)",
                    boxShadow:
                      theme.palette.mode === "dark"
                        ? "0 10px 30px rgba(59, 130, 246, 0.4)"
                        : "0 10px 30px rgba(37, 99, 235, 0.3)",
                  },
                }}
                href="https://github.com/ralph01231"
                target="_blank"
              >
                <GitHub />
              </IconButton>
              <IconButton
                sx={{
                  bgcolor:
                    theme.palette.mode === "dark"
                      ? "rgba(59, 130, 246, 0.1)"
                      : "rgba(37, 99, 235, 0.1)",
                  color: theme.palette.primary.main,
                  border: `2px solid ${
                    theme.palette.mode === "dark"
                      ? "rgba(59, 130, 246, 0.3)"
                      : "rgba(37, 99, 235, 0.3)"
                  }`,
                  width: 56,
                  height: 56,
                  transition: "all 0.3s",
                  "&:hover": {
                    bgcolor: theme.palette.primary.main,
                    color: "white",
                    transform: "translateY(-4px)",
                    boxShadow:
                      theme.palette.mode === "dark"
                        ? "0 10px 30px rgba(59, 130, 246, 0.4)"
                        : "0 10px 30px rgba(37, 99, 235, 0.3)",
                  },
                }}
                href="#"
                target="_blank"
              >
                <LinkedIn />
              </IconButton>
              <IconButton
                sx={{
                  bgcolor:
                    theme.palette.mode === "dark"
                      ? "rgba(59, 130, 246, 0.1)"
                      : "rgba(37, 99, 235, 0.1)",
                  color: theme.palette.primary.main,
                  border: `2px solid ${
                    theme.palette.mode === "dark"
                      ? "rgba(59, 130, 246, 0.3)"
                      : "rgba(37, 99, 235, 0.3)"
                  }`,
                  width: 56,
                  height: 56,
                  transition: "all 0.3s",
                  "&:hover": {
                    bgcolor: theme.palette.primary.main,
                    color: "white",
                    transform: "translateY(-4px)",
                    boxShadow:
                      theme.palette.mode === "dark"
                        ? "0 10px 30px rgba(59, 130, 246, 0.4)"
                        : "0 10px 30px rgba(37, 99, 235, 0.3)",
                  },
                }}
                href="mailto:your.email@example.com"
              >
                <Email />
              </IconButton>
            </Stack>
          </Box>
        </Fade>

        {/* Scroll Indicator */}
        <Fade in={showScrollIndicator} timeout={500}>
          <Box
            sx={{
              position: "absolute",
              bottom: -90,
              left: "50%",
              transform: "translateX(-50%)",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              gap: 1,
              cursor: "pointer",
              animation: "bounce 2s infinite",
              transition: "opacity 0.3s",
              "&:hover": {
                opacity: 0.8,
              },
              "@keyframes bounce": {
                "0%, 100%": {
                  transform: "translateX(-50%) translateY(0)",
                },
                "50%": {
                  transform: "translateX(-50%) translateY(10px)",
                },
              },
            }}
            onClick={() => {
              window.scrollTo({
                top: window.innerHeight,
                behavior: "smooth",
              });
            }}
          >
            <Typography
              variant="caption"
              sx={{
                color: theme.palette.text.primary,
                fontWeight: 600,
                letterSpacing: "0.1em",
                textTransform: "uppercase",
                fontSize: "0.65rem",
                opacity: 0.7,
              }}
            >
              Scroll Down
            </Typography>
            <Box
              sx={{
                width: 25,
                height: 45,
                border: `2px solid ${
                  theme.palette.mode === "dark"
                    ? "rgba(255,255,255,0.3)"
                    : "rgba(0,0,0,0.3)"
                }`,
                borderRadius: "20px",
                position: "relative",
                "&::before": {
                  content: '""',
                  position: "absolute",
                  top: 8,
                  left: "50%",
                  transform: "translateX(-50%)",
                  width: 4,
                  height: 8,
                  borderRadius: "2px",
                  bgcolor:
                    theme.palette.mode === "dark"
                      ? "rgba(255,255,255,0.5)"
                      : "rgba(0,0,0,0.5)",
                  animation: "scroll 1.5s infinite",
                },
                "@keyframes scroll": {
                  "0%": {
                    opacity: 1,
                    transform: "translateX(-50%) translateY(0)",
                  },
                  "100%": {
                    opacity: 0,
                    transform: "translateX(-50%) translateY(20px)",
                  },
                },
              }}
            />
          </Box>
        </Fade>
      </Container>
    </Box>
  );
}
